___CSS_LOADER_EXPORT___.push([module.id, `.container {
    margin: 24px;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 48px);
    font-family: 'Adobe Clean', sans-serif;
}

.divider {
    border: none;
    height: 1px;
    background-color: #e1e1e1;
    margin: 16px 0;
}

.app-header {
    margin-bottom: 16px;
}

.app-header h1 {
    margin: 0 0 8px 0;
    font-size: 24px;
    font-weight: 700;
}

.subtitle {
    margin: 0 0 4px 0;
    color: #666;
    font-size: 14px;
}

.version-info {
    font-size: 12px;
    color: #888;
    margin-top: 4px;
}

.app-content {
    flex: 1;
    overflow-y: auto;
    padding: 16px 0;
}

.state-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 24px;
    height: 100%;
}

.error-message {
    color: #d7373f;
    font-weight: 500;
    margin-bottom: 16px;
}

.description {
    margin-bottom: 24px;
    color: #666;
    text-align: center;
    max-width: 500px;
}

.api-key-input {
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 24px;
}

.api-key-field {
    width: 100%;
    padding: 10px 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    margin-bottom: 12px;
}

.api-key-field:focus {
    border-color: #1473e6;
    outline: none;
}

.api-key-note {
    font-size: 12px;
    color: #666;
    margin: 8px 0 24px 0;
    text-align: center;
}

.video-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
    width: 100%;
    max-width: 500px;
    margin: 20px 0;
}

.video-card {
    cursor: pointer;
    transition: all 0.2s ease;
    border: 2px solid transparent;
}

.video-card.selected {
    border-color: #1473e6;
    background-color: rgba(20, 115, 230, 0.05);
}

.video-info {
    padding: 12px;
}

.video-info h3 {
    margin: 0 0 8px 0;
    font-size: 16px;
}

.video-info p {
    margin: 0;
    font-size: 14px;
    color: #666;
}

.analysis-steps {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 500px;
    margin: 24px 0;
}

.step {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 80px;
}

.step-number {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background-color: #eaeaea;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    margin-bottom: 8px;
}

.step.active .step-number {
    background-color: #1473e6;
    color: white;
}

.step.completed .step-number {
    background-color: #33ab84;
    color: white;
}

.step-text {
    font-size: 12px;
    text-align: center;
}

.video-preview {
    width: 100%;
    max-width: 600px;
    margin: 24px 0;
}

.video-placeholder {
    background-color: #333;
    width: 100%;
    height: 240px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 16px;
    border-radius: 4px;
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.video-placeholder:hover .play-icon,
.video-placeholder:hover .pause-icon {
    opacity: 1;
    transform: scale(1.1);
}

.video-placeholder.playing::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(to right, #1473e6, #33ab84);
    animation: playback-progress 10s linear infinite;
}

@keyframes playback-progress {
    0% { width: 0; }
    100% { width: 100%; }
}

.play-icon, .pause-icon {
    color: white;
    font-size: 48px;
    opacity: 0.8;
    transition: all 0.2s ease;
    z-index: 2;
}

.video-time-indicator {
    position: absolute;
    bottom: 10px;
    right: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 14px;
}

.timeline-container {
    margin-bottom: 16px;
}

.timeline-slider {
    width: 100%;
    margin: 10px 0;
}

.timeline {
    height: 24px;
    background-color: #eaeaea;
    position: relative;
    border-radius: 4px;
    margin-bottom: 8px;
}

.viral-segment {
    position: absolute;
    height: 100%;
    background-color: #33ab84;
    border-radius: 4px;
}

.playhead {
    position: absolute;
    height: 100%;
    width: 2px;
    background-color: #e34850;
    top: 0;
}

.time-labels {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    color: #666;
}

.segment-info {
    background-color: #f5f5f5;
    padding: 12px;
    border-radius: 4px;
    margin-top: 16px;
}

.segment-info p {
    margin: 8px 0;
    font-size: 14px;
}

.button-row {
    display: flex;
    gap: 12px;
    margin-top: 20px;
}

/* Debug Panel Styles */
.debug-panel {
    width: 100%;
    max-width: 800px;
    margin-top: 24px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 16px;
    font-size: 14px;
}

.debug-panel h3 {
    margin-top: 0;
    margin-bottom: 16px;
    font-size: 18px;
    color: #333;
    border-bottom: 1px solid #ddd;
    padding-bottom: 8px;
}

.debug-section {
    margin-bottom: 20px;
}

.debug-section h4 {
    margin: 0 0 8px 0;
    font-size: 16px;
    color: #444;
}

.debug-content {
    background-color: white;
    border: 1px solid #eee;
    border-radius: 4px;
    padding: 12px;
    max-height: 200px;
    overflow-y: auto;
    font-family: monospace;
    font-size: 13px;
    line-height: 1.4;
}

.debug-content.transcript {
    white-space: pre-wrap;
    color: #333;
}

.debug-content pre {
    margin: 0;
    font-family: 'Courier New', monospace;
    font-size: 12px;
    line-height: 1.5;
    overflow-x: auto;
}

.timestamps-container {
    padding: 8px;
}

.timestamps-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 8px;
}

.timestamp-item {
    background-color: #f0f0f0;
    border-radius: 4px;
    padding: 6px 10px;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.timestamp-item .duration {
    color: #666;
    font-size: 11px;
    margin-left: 4px;
}

.debug-section p {
    margin: 4px 0;
    line-height: 1.4;
}

.debug-section.error .debug-content {
    background-color: #fff0f0;
    border-color: #ffcccc;
    color: #d00;
}

.timestamp {
    display: inline-block;
    background-color: #eee;
    padding: 2px 6px;
    border-radius: 4px;
    margin-right: 8px;
    margin-bottom: 8px;
    font-size: 12px;
}

.success-message {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
}

.success-icon {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #33ab84;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    margin-right: 12px;
}

/* Loading indicators */
.loading-indicator {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 3px solid #eaeaea;
    border-top-color: #1473e6;
    animation: spin 1s linear infinite;
    margin-bottom: 16px;
}

.loading-bar {
    width: 100%;
    max-width: 300px;
    height: 4px;
    background-color: #eaeaea;
    position: relative;
    overflow: hidden;
    margin: 16px 0;
}

.loading-bar::after {
    content: '';
    position: absolute;
    width: 30%;
    height: 100%;
    background-color: #1473e6;
    animation: loading-bar 1.5s ease-in-out infinite;
}

@keyframes spin {
    to { transform: rotate(360deg); }
}

@keyframes loading-bar {
    0% { left: -30%; }
    100% { left: 100%; }
}
